<template>
  <div class="Signature">
    <HeaderNavigation />
    <!-- 竖屏 -->
    <SignatureVertical v-if="!status" />
    <!-- 横屏 -->
    <SignaturesAcross v-if="status" />
    <!-- 建议弹窗 -->
    <van-popup v-model="popupShow" class="popupShowStyle">
      <div>
        <img class="popupShowImg" src="@/assets/image/mobilePhone.png" alt="" />
      </div>
      <h2>請使用橫式瀏覽</h2>
    </van-popup>
  </div>
</template>

<script>
import HeaderNavigation from "@/components/HeaderNavigation";
import SignatureVertical from "./components/SignatureVertical";
import SignaturesAcross from "./components/SignaturesAcross";
export default {
  name: "Signature",
  components: {
    HeaderNavigation,
    SignatureVertical,
    SignaturesAcross
  },
  data() {
    return {
      succeedShow: false,
      beDefeatedShow: false,
      popupShow: true,
      status: false
    };
  },
  mounted() {
    // 监听resize方法
    window.addEventListener("resize", this.renderResize, false);
  },
  beforeDestroy() {
    // 移除监听
    window.removeEventListener("resize", this.renderResize, false);
  },
  methods: {
    renderResize() {
      // 判断横竖屏
      let width = document.documentElement.clientWidth;
      let height = document.documentElement.clientHeight;
      if (width > height) {
        this.status = true;
        this.popupShow = false;
      } else {
        this.status = false;
        this.popupShow = true;
      }
    }
  }
};
</script>

<style lang="scss">
.Signature {
  .popupShowStyle {
    background: none;
    text-align: center;
    width: 140px;
    padding: 10px;
    div {
      .popupShowImg {
        width: 50px;
        height: 100px;
        animation: popupShowImg 2s infinite;
      }
      @keyframes popupShowImg {
        0% {
          transform: rotate(0);
        }
        70% {
          transform: rotate(-90deg);
        }
        90% {
          transform: rotate(0);
        }
        0% {
          transform: rotate(0);
        }
      }
    }
    h2 {
      font-size: 14px;
      color: #ffffff;
      line-height: 40px;
    }
  }
  // .van-overlay {
  //   background-color: rgba(0, 0, 0, 0.8);
  // }
}
</style>
