<template>
  <div>
    <div class="SignaturesAcrossStyle">
      <div class="SignaturesAcross">
        <h2>客戶簽名</h2>
        <h3>
          Customer signature
          <em>
            金額：${{ this.$route.query.Price ? this.$route.query.Price : 0 }}
          </em>
        </h3>
        <div id="canvass"></div>
        <div class="SignaturesAcrossBottom">
          <div id="clearCanvass">重新簽名</div>
          <div id="saveCanvass" @click="saveSign()">送出</div>
        </div>
      </div>
    </div>
    <!-- 失败弹窗 -->
    <!-- 失败弹窗 -->
    <van-popup v-model="beDefeatedShow" class="beDefeatedShowStyle">
      <img src="@/assets/image/failed.png" alt="" />
      <h2>{{ failureTitle }}</h2>
      <div @click="beDefeatedShowClick()">上一步</div>
    </van-popup>
    <!-- 成功弹窗 -->
    <!-- 成功弹窗 -->
    <van-popup v-model="succeedShow" class="succeedShowStyle">
      <img src="@/assets/image/succeed.png" alt="" />
      <h2>{{ successTitle }}</h2>
      <div @click="succeedShowClick()">我知道了</div>
    </van-popup>
    <SubmitLoading :show="LoadingStates" />
  </div>
</template>

<script>
import {
  POST_CreateWaterInstallComplete,
  POST_CreateWaterMaintainComplete,
  POST_GetIncident,
} from "@/utils/api";
import SubmitLoading from "@/components/SubmitLoading";
export default {
  components: {
    SubmitLoading,
  },
  data() {
    return {
      LoadingStates: false,
      succeedShow: false,
      beDefeatedShow: false,
      userInfoList: "",
      failureTitle: "",
      successTitle: "",
    };
  },
  created() {
    this.GetIncident();
  },
  mounted() {
    this.init_lineCanvas();
  },
  methods: {
    // 获取信息
    GetIncident() {
      var UofIds = {
        DistributionUofId: sessionStorage.getItem("DistributionUofId"),
      };
      POST_GetIncident(UofIds).then((response) => {
        for (let i = 0; i < response.Data.length; i++) {
          if (response.Data[i].IncidentNo == this.$route.query.CustId) {
            this.userInfoList = response.Data[i];
          }
        }
      });
      if (this.$route.query.type == "install") {
        this.failureTitle = "安裝失敗";
        this.successTitle = "安裝完成";
      } else if (this.$route.query.type == "replace") {
        this.failureTitle = "濾心更換失敗";
        this.successTitle = "濾心更換完成";
      }
    },
    // 安装完成
    succeedShowClick() {
      this.succeedShow = false;
      if (this.$route.query.type == "install") {
        this.$router.push("/InstallReplacement/Replacement");
      } else if (this.$route.query.type == "replace") {
        this.$router.push("/InstallReplacement");
      }
    },
    beDefeatedShowClick() {
      this.beDefeatedShow = false;
      this.$router.go(-1);
    },
    // 保存签名
    saveSign() {
      if (!sessionStorage.getItem("status")) {
        return false;
      }
      this.LoadingStates = true;
      setTimeout(() => {
        /* 电子签名base64图片上传*/
        if (this.$route.query.type == "install") {
          var UofIds = {
            DistributionUofId: sessionStorage.getItem("DistributionUofId"),
            ModelName: this.$route.query.name,
            Mfno: this.$route.query.Mfno,
            Charge: this.$route.query.Price,
            InstallId: this.$route.query.id,
            SignatureImage: sessionStorage.getItem("imgBase64"),
            WaterPressure: this.$route.query.WaterPressure,
            TDS: this.$route.query.TDS,
            Chlorine: this.$route.query.Chlorine,
            PH: this.$route.query.PH,
          };
          POST_CreateWaterInstallComplete(UofIds).then((response) => {
            this.LoadingStates = false;
            if (response.State) {
              this.succeedShow = true;
            } else {
              this.beDefeatedShow = true;
            }
          });
        } else if (this.$route.query.type == "replace") {
          let Filter1DelayMonth = "";
          let Filter1DelayMonthMemo = "";
          let Filter1Mfno = "";
          let Filter1Charge = "";
          let Filter2DelayMonth = "";
          let Filter2DelayMonthMemo = "";
          let Filter2Mfno = "";
          let Filter2Charge = "";
          let Filter3DelayMonth = "";
          let Filter3DelayMonthMemo = "";
          let Filter3Mfno = "";
          let Filter3Charge = "";
          let Filter4DelayMonth = "";
          let Filter4DelayMonthMemo = "";
          let Filter4Mfno = "";
          let Filter4Charge = "";
          let Filter5DelayMonth = "";
          let Filter5DelayMonthMemo = "";
          let Filter5Mfno = "";
          let Filter5Charge = "";
          let Filter1ServiceResult = "";
          let Filter2ServiceResult = "";
          let Filter3ServiceResult = "";
          let Filter4ServiceResult = "";
          let Filter5ServiceResult = "";
          if (this.$store.state.extension.extension1 && this.$store.state.extension.extension1.CustId) {
            Filter1ServiceResult = "W036";
            Filter1DelayMonth = this.$store.state.extension.extension1.extensionDate;
            Filter1DelayMonthMemo = this.$store.state.extension.extension1.extensionReason;
          }
          if (this.$store.state.replace.replace1 && this.$store.state.replace.replace1.CustId) {
            Filter1ServiceResult = "W001";
            Filter1Mfno = this.$store.state.replace.replace1.Mfno;
            Filter1Charge = this.$store.state.replace.replace1.Price;
          }
          if (this.$store.state.extension.extension2 && this.$store.state.extension.extension2.CustId) {
            Filter2ServiceResult = "W036";
            Filter2DelayMonth = this.$store.state.extension.extension2.extensionDate;
            Filter2DelayMonthMemo = this.$store.state.extension.extension2.extensionReason;
          }
          if (this.$store.state.replace.replace2 && this.$store.state.replace.replace2.CustId) {
            Filter2ServiceResult = "W002";
            Filter2Mfno = this.$store.state.replace.replace2.Mfno;
            Filter2Charge = this.$store.state.replace.replace2.Price;
          }
          if (this.$store.state.extension.extension3 && this.$store.state.extension.extension3.CustId) {
            Filter3ServiceResult = "W036";
            Filter3DelayMonth = this.$store.state.extension.extension3.extensionDate;
            Filter3DelayMonthMemo = this.$store.state.extension.extension3.extensionReason;
          }
          if (this.$store.state.replace.replace3 && this.$store.state.replace.replace3.CustId) {
            Filter3ServiceResult = "W003";
            Filter3Mfno = this.$store.state.replace.replace3.Mfno;
            Filter3Charge = this.$store.state.replace.replace3.Price;
          }
          if (this.$store.state.extension.extension4 && this.$store.state.extension.extension4.CustId) {
            Filter4ServiceResult = "W036";
            Filter4DelayMonth = this.$store.state.extension.extension4.extensionDate;
            Filter4DelayMonthMemo = this.$store.state.extension.extension4.extensionReason;
          }
          if (this.$store.state.replace.replace4 && this.$store.state.replace.replace4.CustId) {
            Filter4ServiceResult = "W004";
            Filter4Mfno = this.$store.state.replace.replace4.Mfno;
            Filter4Charge = this.$store.state.replace.replace4.Price;
          }
          if (this.$store.state.extension.extension5 && this.$store.state.extension.extension5.CustId) {
            Filter5ServiceResult = "W036";
            Filter5DelayMonth = this.$store.state.extension.extension5.extensionDate;
            Filter5DelayMonthMemo = this.$store.state.extension.extension5.extensionReason;
          }
          if (this.$store.state.replace.replace5 && this.$store.state.replace.replace5.CustId) {
            Filter5ServiceResult = "W005";
            Filter5Mfno = this.$store.state.replace.replace5.Mfno;
            Filter5Charge = this.$store.state.replace.replace5.Price;
          }
          let dataForm = {
            ProductId: this.$route.query.ProductId,
            IncidentNo: this.$route.query.IncidentNo,
            Signature: sessionStorage.getItem("imgBase64"),
            Filter1DelayMonth: Number(Filter1DelayMonth),
            Filter1DelayMonthMemo: Filter1DelayMonthMemo,
            Filter1Mfno: Filter1Mfno,
            Filter1Charge: Number(Filter1Charge),
            Filter1ServiceResult: Filter1ServiceResult,
            Filter2DelayMonth: Number(Filter2DelayMonth),
            Filter2DelayMonthMemo: Filter2DelayMonthMemo,
            Filter2Mfno: Filter2Mfno,
            Filter2Charge: Number(Filter2Charge),
            Filter2ServiceResult: Filter2ServiceResult,
            Filter3DelayMonth: Number(Filter3DelayMonth),
            Filter3DelayMonthMemo: Filter3DelayMonthMemo,
            Filter3ServiceResult: Filter3ServiceResult,
            Filter3Mfno: Filter3Mfno,
            Filter3Charge: Number(Filter3Charge),
            Filter4DelayMonth: Number(Filter4DelayMonth),
            Filter4DelayMonthMemo: Filter4DelayMonthMemo,
            Filter4ServiceResult: Filter4ServiceResult,
            Filter4Mfno: Filter4Mfno,
            Filter4Charge: Number(Filter4Charge),
            Filter5DelayMonth: Number(Filter5DelayMonth),
            Filter5DelayMonthMemo: Filter5DelayMonthMemo,
            Filter5ServiceResult: Filter5ServiceResult,
            Filter5Mfno: Filter5Mfno,
            Filter5Charge: Number(Filter5Charge)
          };
          POST_CreateWaterMaintainComplete(dataForm).then((response) => {
            this.LoadingStates = false;
            if (response.State) {
              this.succeedShow = true;
            } else {
              this.beDefeatedShow = true;
            }
          });
        }
      }, 500);
    },
    init_lineCanvas() {
      // document.addEventListener(
      //   "touchmove",
      //   function(event) {
      //     event.preventDefault();
      //   },
      //   { passive: false }
      // );
      new this.lineCanvas({
        el: document.getElementById("canvass"), // 绘制canvas的父级div
        clearEl: document.getElementById("clearCanvass"), // 清除按钮
        saveEl: document.getElementById("saveCanvass"), // 保存按钮
        linewidth: 3, // 线条粗细，选填
        color: "black", // 线条颜色，选填
        background: "#ffffff", // 线条背景，选填
      });
    },
    /* 电子签名手写板 */
    lineCanvas(obj) {
      this.linewidth = 1;
      this.color = "#000000";
      this.background = "#ffffff";
      for (var i in obj) {
        this[i] = obj[i];
      }
      this.canvass = document.createElement("canvas");
      this.el.appendChild(this.canvass);
      this.cxt = this.canvass.getContext("2d");
      var width = document.body.scrollWidth;
      this.canvass.width = width;
      this.canvass.height = 194;
      this.cxt.fillStyle = this.background;
      this.cxt.fillRect(0, 0, this.canvass.width, this.canvass.width);
      this.cxt.strokeStyle = this.color;
      this.cxt.lineWidth = this.linewidth;
      this.cxt.lineCap = "round";
      // 开始绘制
      this.canvass.addEventListener(
        "touchstart",
        function(e) {
          e.preventDefault();
          this.cxt.beginPath();
          this.cxt.moveTo(
            e.changedTouches[0].pageX,
            e.changedTouches[0].pageY - 131
          );
        }.bind(this),
        false
      );
      // 绘制中
      this.canvass.addEventListener(
        "touchmove",
        function(e) {
          e.preventDefault();
          sessionStorage.setItem("status", true);
          this.cxt.lineTo(
            e.changedTouches[0].pageX,
            e.changedTouches[0].pageY - 131
          );
          this.cxt.stroke();
        }.bind(this),
        false
      );
      // 结束绘制
      this.canvass.addEventListener(
        "touchend",
        function() {
          this.cxt.closePath();
        }.bind(this),
        false
      );
      // 清除画布
      this.clearEl.addEventListener(
        "click",
        function() {
          sessionStorage.removeItem("status");
          this.cxt.clearRect(0, 0, this.canvass.width, this.canvass.height);
        }.bind(this),
        false
      );
      document.getElementById("clearCanvass").click();
      // 保存图片，直接转base64
      this.saveEl.addEventListener(
        "click",
        function() {
          var imgBase64 = this.canvass.toDataURL();
          sessionStorage.setItem("imgBase64", imgBase64);
        }.bind(this),
        false
      );
    },
  },
};
</script>

<style lang="scss">
.SignaturesAcrossStyle {
  padding: 20px;
  .SignaturesAcross {
    padding: 20px;
    background: rgba(255, 255, 255, 1);
    border-radius: 8px;
    h2 {
      font-size: 22px;
      font-weight: 500;
      color: rgba(61, 61, 61, 1);
      line-height: 30px;
    }
    h3 {
      font-size: 12px;
      font-weight: 500;
      color: rgba(134, 134, 134, 1);
      line-height: 17px;
      margin-bottom: 14px;
      em {
        float: right;
      }
    }
    #canvass canvas {
      display: block;
      background: #f3f3f3;
      border-radius: 8px;
      margin: 0 auto;
      width: 100%;
    }

    .SignaturesAcrossBottom {
      margin-top: 16px;
      display: flex;
      justify-content: space-around;
      #clearCanvass {
        width: 40%;
        height: 38px;
        background: rgba(229, 239, 255, 1);
        border-radius: 12px;
        font-size: 16px;
        font-weight: 500;
        color: rgba(28, 118, 254, 1);
        line-height: 38px;
        text-align: center;
      }

      #saveCanvass {
        width: 40%;
        height: 38px;
        background: linear-gradient(
          180deg,
          rgba(28, 118, 254, 1) 0%,
          rgba(28, 118, 254, 1) 100%
        );
        box-shadow: 0px 10px 40px 0px rgba(28, 118, 254, 0.3);
        border-radius: 12px;
        font-size: 16px;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        line-height: 38px;
        text-align: center;
      }
    }
  }
}
.beDefeatedShowStyle {
  width: 280px;
  padding: 40px 0;
  height: 176px;
  background: rgba(255, 255, 255, 1);
  border-radius: 15px;
  text-align: center;
  h2 {
    padding: 26px 0;
    font-size: 15px;
    font-weight: 500;
    color: rgba(61, 61, 61, 1);
    line-height: 20px;
  }
  div {
    margin: 0 auto;
    width: 150px;
    height: 40px;
    background: rgba(28, 118, 254, 1);
    box-shadow: 0px 12px 30px 0px rgba(28, 118, 254, 0.15);
    border-radius: 6px;
    font-size: 15px;
    font-weight: 500;
    color: rgba(255, 255, 255, 1);
    line-height: 40px;
  }
}
.succeedShowStyle {
  width: 280px;
  padding: 40px 0;
  height: 176px;
  background: rgba(255, 255, 255, 1);
  border-radius: 15px;
  text-align: center;
  h2 {
    padding: 26px 0;
    font-size: 15px;
    font-weight: 500;
    color: rgba(61, 61, 61, 1);
    line-height: 20px;
  }
  div {
    margin: 0 auto;
    width: 150px;
    height: 40px;
    background: rgba(28, 118, 254, 1);
    box-shadow: 0px 12px 30px 0px rgba(28, 118, 254, 0.15);
    border-radius: 6px;
    font-size: 15px;
    font-weight: 500;
    color: rgba(255, 255, 255, 1);
    line-height: 40px;
  }
}

@media screen and (orientation: portrait) {
  .SignaturesAcrossStyle {
    display: none;
  }
}
@media screen and (orientation: landscape) {
  .SignaturesAcrossStyle {
    display: block;
  }
}
</style>
